/* eslint-disable max-len */
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MenuController } from '@ionic/angular';

import { Device } from '@capacitor/device';

import { AuthService } from 'src/app/shared/services/auth/auth.service';
import CredenciaisDTO from 'src/app/shared/interfaces/Credencias.dto';
import { PreferencesService } from '../../shared/services/preferences/preferences.service';
import { ThemeService } from 'src/app/shared/services/theme/theme.service';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { Subscription } from 'rxjs';
import { changeBaseURL } from 'src/config/api.config';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit, OnDestroy {

  credenciais: CredenciaisDTO = {
    email: '',
    senha: '',
    current_terminal: '',
  };

  private subscriptions = new Subscription();

  constructor(
    private router: Router,
    private menu: MenuController,
    private auth: AuthService,
    private preferencesService: PreferencesService,
    private themeService: ThemeService,
    private storage: StorageService,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    document.body.setAttribute('color-theme', 'light');
    this.themeService.switchTheme('saga-blue');
    this.menu.close();

  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ionViewWillEnter() {
    document.body.setAttribute('color-theme', 'light');
    this.themeService.switchTheme('saga-blue');
    this.menu.close();
    this.menu.enable(false);
  }

  ionViewDidEnter() {
    try {
      const sub = this.auth.refreshToken()
        .subscribe(response => {
          const localUser = this.storage.getLocalUser();
          localUser.token = response.token;
          localUser.refresh_token = response.refresh_token;
          this.auth.loginSucesso(localUser);

          // setupWebSockets();

          this.router.navigateByUrl('home');
        }, error => {
          this.menu.enable(true);
          this.menu.close();
          this.menu.enable(false);
          this.auth.logout();
        });
      this.subscriptions.add(sub);
    } catch (e) {
      this.menu.enable(true);
      this.menu.close();
      this.menu.enable(false);
    }
  }

  ionViewWillLeave() {
    const theme = this.preferencesService.getThemePreference();
    document.body.setAttribute('color-theme', theme);
    if (theme === 'dark') {
      this.themeService.switchTheme('bootstrap4-dark-yellow');
    } else {
      this.themeService.switchTheme('saga-blue');
    }

    this.menu.enable(true);
  }

  async login() {
    if (this.controlLoginButton()) {
      return;
    }

    if (this.credenciais.email.trim().toUpperCase().includes('URL:')) {
      this.forceNewUrl();
      return;
    }

    const { uuid } = await Device.getId();

    this.credenciais.current_machine_id = uuid;

    const sub = this.auth.authenticate(this.credenciais).subscribe((response) => {
      this.auth.loginSucesso(response);
      const url =
        this.activatedRoute.snapshot.queryParamMap.get('redirect') ||
        'home';

      //setupWebSockets();

      this.router.navigateByUrl(url);
    },
      error => { });


    this.subscriptions.add(sub);
  }

  private forceNewUrl() {
    const { email } = this.credenciais;
    const newUrl = email.trim().toUpperCase().split('URL:').pop().trim();
    changeBaseURL(newUrl);
  }

  async goToRequestReset() {
    this.router.navigateByUrl('password-reset');
  }

  setFocus(nextElement) {
    nextElement.setFocus();
  }

  controlLoginButton(): boolean {
    if (this.credenciais.email && this.credenciais.senha) {
      return false;
    }
    return true;
  }


}
